.btn.active.focus, 
.btn.active:focus, 
.btn.focus, 
.btn.focus:active, 
.btn:active:focus, 
.btn:focus {
    outline: none !important;
    box-shadow: none;
  }

  
/**
 *
 * Begin Header
 *
*/
header{
    min-height: 30vh;
} 
/* // End Of Header */ 

/**
 * Begin Min Content
*/
.main-content {
    min-height: 46vh;
}
/* //End of Mian Content */


/**
 * Begin Footer
*/
footer{
    min-height: 12vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $secondary;
    color: white;
    margin-top: 5rem;
}

/* //End Of Footer */ 


.free-background{
    width: 100%;
    position: absolute;
    top:0;
    z-index: -222;
    min-height: 60vh;
    background-color: $primary;
}
.card{
    border: none;
    box-shadow: 0.0625rem 0.125rem 0.375rem rgba(0,0,0,0.1);

    .card-header{
        box-shadow: 0.0625rem 0.125rem 0.375rem rgba(0,0,0,0.1);
    }
}

.btn-primary{
    color: white;
}

.hide-on-screen{
    display: none;
}

.h-30{
    height: 30vh;
}

.mt-2vh{
    margin-top: 2 * 10vh;
}




